<template>
  <i :class="'ccb-icon-' + name" @click="handleClick"/>
</template>

<script>
/**
 * 字体图标, 暂时先只支持font-class引用
 */
export default {
  name: 'CcbIcon',
  props: {
    name: {
      type: String,
      required: true
    }
  },
  methods: {
    handleClick (e) {
      this.$emit('click', e)
    }
  }
}
</script>
