<template>
  <form :class="['ccb-form',
    'ccb-form-' + layout
  ]">
    <slot></slot>
  </form>
</template>

<script>

import { CcbRow, CcbCol } from '@/ui/layout/flex'
import { noop } from '@/tools/util'

export default {
  name: 'ccb-form',
  components: {
    CcbRow,
    CcbCol
  },
  provide () {
    return {
      ccbForm: this // 给下面的ccb-form-item子组件使用
    }
  },
  props: {
    layout: {
      type: String,
      default: 'horizontal',
      validator: v => ['horizontal', 'vertical', 'inline'].indexOf(v) >= -1
    },
    col: {
      type: Number,
      default: 0
    },
    gutter: {
      type: Number,
      default: 0
    },
    model: {
      type: Object,
      // required: true,
      default: () => ({})
    }
  },
  data () {
    return {
      items: [] // 储存form-item组件
    }
  },
  created () {
    this.$on('ccb-form-addItem', item => {
      this.items.push(item)
    })
    this.$on('ccb-form-removeItem', item => {
      const idx = this.items.indexOf(item)
      if (idx > -1) {
        this.items.splice(idx, 1)
      }
    })
  },
  methods: {
    // 验证表单
    validateForm (fields, cb = noop) {
      let validateItems
      // 没指定则全部验证
      if (!Array.isArray(fields)) {
        cb = fields || noop
        validateItems = this.items
      } else {
        // 找出field相同的
        validateItems = this.items.filter(item => fields.some(field => field === item.field))
      }
      let errors = []
      let count = 0
      validateItems.forEach(item => {
        // 要验证的form-item组件
        item.validate((errs, errItems) => {
          if (errs) {
            errors = errors.concat(errs)
          }
          // 由于可能有异步验证, 所以这样判断结束
          if (++count === validateItems.length) {
            cb(errors.length === 0, errors)
          }
        })
      })
    },
    resetForm (fields) {
      this.batchMethod(fields, 'resetItem')
    },
    clearValidate (fields) {
      this.batchMethod(fields, 'clearValidate')
    },
    batchMethod (fields = [], funcName) {
      const items = fields.length === 0
        ? this.items
        : this.items.filter(item => fields.indexOf(item.field) > -1)
      items.forEach(item => {
        item[funcName]()
      })
    }
  }
}
</script>

