<template>
    <div class="ccb-input-div">
        <input class="ccb-input" :style="cssStyles" :class="classes" :value="value" :disabled="disabled" :readonly="readonly"
               :placeholder="placeholder" @click="clickHandler" @change="changeHandler" @input="inputHandler" @focus="focusHandler" @mouseover="hoverHandler">
        <span v-if="$slots.suffix || suffixIcon" class="ccb-input-suffix">
            <slot v-if="$slots.suffix" name="suffix"></slot>
            <i v-else class="ccb-input-suffix-icon" :class="suffixIcon"></i>
        </span>
    </div>
</template>

<script>
export default {
  name: 'CcbInput',
  props: {
    value: [String, Number],
    inputType: String,
    suffixIcon: String,
    width: {
      type: String,
      default: '400px'
    },
    height: {
      type: String,
      default: '30px'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    cssStyle: Object,
    styleClass: String
  },
  computed: {
    cssStyles: function () {
      let a = {
        /*  width: this.width,
                    height: this.height, */
        lineHeight: this.lineHeight
      }
      if (this.cssStyle) {
        this.copyParams(a, this.cssStyle)
      }
      return a
    },
    styleClassName: function () {
      return 'ccb-input-' + this.styleClass
    },
    classes: function () {
      if (this.styleClass === undefined) return ''
      let s = this.styleClassName
      if (this.disabled) s += ' is-disabled'
      return s
    }
  },
  methods: {
    copyParams: function (target, src) {
      if (target && src && typeof src === 'object') {
        for (var p in src) {
          target[p] = src[p]
        }
      }
      return target
    },
    inputHandler: function (evt) {
      if (this.disabled) return
      this.$emit('input', evt.target.value)
    },
    focusHandler: function (evt) {
      if (this.disabled) return
      this.$emit('focus', evt.target.value)
    },
    hoverHandler: function (evt) {
      if (this.disabled) return
      this.$emit('hover', evt.target.value)
    },
    changeHandler (evt) {
      this.$emit('change', evt)
    },
    clickHandler (evt) {
      this.$emit('click', evt)
    }
  }
}
</script>

<style>
    .ccb-input-div {
        position: relative;
        display: inline-block;
        width: 100%;
    }
    .ccb-input:hover {
        border-color: #3F79E5;
    }
    .ccb-input:focus {
        border-color: #3F79E5;
    }
    .ccb-input.is-disabled {
        background-color: #F5F5F5;
        border: 1px solid #E6E6E6;
    }
    .ccb-input {
        width: 100%;
        height: 30px;
        line-height: 30px;
        border-radius: 4px;
        padding: 0 15px;
        border: 1px solid #dcdfe6;
        outline: none;
    }
    .ccb-input-suffix {
        position: absolute;
        right: 18px;
        top: 50%;
        transform: translateY(-50%);
        pointer-events: none;
    }
    .ccb-input-suffix-icon:after {
        content:'';
        vertical-align: middle;
        height: 100%;
        display: inline-block;
    }
</style>