
import _debounce from 'lodash/debounce'
import _throttle from 'lodash/throttle'

/**
 * 是否是Object
 */
export function isObject (obj) {
  return typeof (obj) === 'object' && obj !== null
}

export function isUndef (v) {
  return v === undefined || v === null
}

export function isDef (v) {
  return v !== undefined && v !== null
}

const _toString = Object.prototype.toString

export function isPlainObject (obj) {
  return _toString.call(obj) === '[object Object]'
}

export function deepClone (obj) {
  if (isPlainObject(obj)) {
    const res = {}
    for (const key in obj) {
      res[key] = deepClone(obj[key])
    }
    return res
  } else if (Array.isArray(obj)) {
    const arr = []
    for (const ele of obj) {
      arr.push(deepClone(ele))
    }
    return arr
  } else {
    return obj
  }
}

export function isPromise (p) {
  return p && (typeof p === 'object' || typeof p === 'function') && typeof p.then === 'function'
}

export function noop () {}

export function mergeOpts ($vm, options) {
  const defaults = {}
  for (let i in $vm.$options.props) {
    if (i !== 'value') {
      defaults[i] = $vm.$options.props[i].default
    }
  }
  const _options = Object.assign({}, defaults, options)
  for (let i in _options) {
    $vm[i] = _options[i]
  }
}

export const debounce = _debounce
export const throttle = _throttle

export function getValueByPath (obj = {}, path = '', strict) {
  let keys = path.split('.')
  let o = obj
  let errMsg
  let i = 0
  for (let len = keys.length; i < len - 1; i++) {
    if (keys[i] in o) {
      o = o[keys[i]]
    } else {
      errMsg = `${JSON.stringify(obj)}中找不到${path}的值!!!`
      console.warn(errMsg)
      if (strict) {
        throw new Error(errMsg)
      }
      break
    }
  }
  return {
    o,
    k: keys[i],
    v: o ? o[keys[i]] : null
  }
}

export function jsxClasses (...args) {
  let cls = []
  args.forEach(arg => {
    if (Array.isArray(arg)) {
      cls = cls.concat(arg)
    } else if (isObject(arg)) {
      for (const c in arg) {
        if (arg[c]) {
          cls.push(c)
        }
      }
    } else if (typeof (arg) === 'string') {
      cls.push(arg)
    }
  })
  return cls
}