import modalManager from './modal-manager'

export default {
  methods: {
    getNextZIndex () {
      return modalManager.nextZIndex()
    },
    openModal () {
      modalManager.openModal(this)
      if (this.$el) { // 比如messagebox
        this.$el.style.zIndex = modalManager.nextZIndex()
      }
    },
    closeModal () {
      modalManager.closeModal(this)
    }
  },
  mounted () {
    if (this.$el.style.zIndex !== modalManager.currZIndex()) {
      this.$el.style.zIndex = modalManager.nextZIndex()
    }
  }
}
