import { throttle } from '@/tools/util'

let topScrollEl // body or documentElement

const getScrollParent = function (el) {
  const parent = el.parentNode
  if (!parent) {
    return el
  }
  if (parent === document) { // 已经来到了顶层, 返回含有scrollTop的值吧, body或者documentElement(浏览器差异)
    if (topScrollEl) { // 免得每次再去额外判断
      return topScrollEl
    }
    if (document.body.scrollTop || document.body.scrollLeft) {
      topScrollEl = document.body
    } else if (document.documentElement.scrollTop || document.documentElement.scrollLeft) {
      topScrollEl = document.documentElement
    } else { // 压根还没滑动的情况, 暂时不知道该用body还是documentElement
      return document.documentElement
    }
    return topScrollEl
  }
  if ( // 直接判断x和y, 因为如果overflow-x和overflow-y不一样的话, overflow返回空串
    ['scroll', 'auto'].indexOf(getStyleComputedProperty(parent, 'overflow-x')) !== -1 ||
    ['scroll', 'auto'].indexOf(getStyleComputedProperty(parent, 'overflow-y')) !== -1
  ) {
    return parent
  }
  return getScrollParent(parent)
}

const getStyleComputedProperty = function (el, property) {
  var css = getComputedStyle(el)
  return css[property]
}

const defaultOptions = {}

export default class popup {
  constructor (el, popupEl, options) {
    this.el = el
    this.popupEl = popupEl
    this.options = Object.assign({}, defaultOptions, options)
    this._initListener()
    this.updatePopup()
  }

  _initListener () {
    this.handler = throttle(this.updatePopup.bind(this), 200)
    this.scrollParent = getScrollParent(this.el)
    window.addEventListener('resize', this.handler)
    this.scrollParent.addEventListener('scroll', this.handler)
  }

  _removeListener () {
    window.removeEventListener('resize', this.handler)
    this.scrollParent.removeEventListener('scroll', this.handler)
  }

  /**
   * 更新位置
   */
  updatePopup () {
    // 与挂载节点的宽度一致
    if (this.options.fitWidth) {
      this.popupEl.style.width = this.el.clientWidth + 'px'
    }
    const elRect = this.el.getBoundingClientRect()
    this.popupEl.style.top = elRect.top + elRect.height + 'px'
    this.popupEl.style.left = elRect.left + 'px'
  }

  destroy () {
    this._removeListener()
  }
}