
<script>
import CcbGridColGroup from './col-group'
import CcbGridCheck from './grid-check'
export default {
  name: 'ccb-grid-header',
  components: {
    CcbGridColGroup,
    CcbGridCheck
  },
  inject: ['$ccbGridMsgCenter'],
  props: {
    columnRows: {
      type: Array,
      default: () => []
    },
    columns: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      selectAll: false
    }
  },
  render (h) {
    return (
      <table border="0" cellspacing="0" cellpadding="0">
        <ccb-grid-col-group columns={this.columns}></ccb-grid-col-group>
        <thead>
          {
            this.columnRows.map(columnRow => {
              return (
                <tr>
                  {
                    columnRow.map(column => {
                      return (
                        <th
                          colspan={column.colspan}
                          rowspan={column.rowspan}
                        >{column.renderHeader.call(this, h, {
                            value: column.title || column.type
                          })}
                        </th>
                      )
                    })
                  }
                </tr>
              )
            })
          }
        </thead>
      </table>
    )
  }
}
</script>

