import msg from './messagebox'
import { mergeOpts } from '@/tools/util'

export default {
  install (Vue) {
    const Cmp = Vue.extend(msg)
    let ins = new Cmp() // 全局只有一个messagebox
    ins.$mount()
    ;['alert', 'confirm'].forEach(type => {
      Vue.$ccb['$' + type] = function (opts = {}) {
        opts.show = true // 打开messagebox
        mergeOpts(ins, opts)
      }
    })
  }
}