
<script>
import CcbGridColGroup from './col-group'
import CcbGridCheck from './grid-check'
import { getValueByPath } from '@/tools/util'

export default {
  name: 'ccb-grid-body',
  inject: ['$ccbGridMsgCenter'],
  components: {
    CcbGridColGroup,
    CcbGridCheck
  },
  props: {
    data: {
      type: Array,
      default: () => []
    },
    columns: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {

    }
  },
  render (h) {
    return (
      <table border="0" cellspacing="0" cellpadding="0">
        <ccb-grid-col-group columns={this.columns}></ccb-grid-col-group>
        <tbody>
          {
            this.data.map((row, rowIdx) => {
              return (
                <tr>
                  {
                    this.columns.map((column, columnIdx) => {
                      if (column.needRender) {
                        return <td>{column.renderCell.call(this, h, {
                          value: getValueByPath(row.data, column.field).v,
                          row,
                          rowIdx,
                          field: column.field
                        })}</td>
                      }
                    })
                  }
                </tr>
              )
            })
          }
        </tbody>
      </table>
    )
  }
}
</script>

