<template>
  <ccb-check v-model="checkValue" :multi="multi" :options="opt"></ccb-check>
</template>

<script>
import { isDef } from '@/tools/util'
import CcbCheck from '@/ui/form/check/check'

let gridCheckValue = 1

export default {
  name: 'ccb-grid-check',
  components: {
    CcbCheck
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    multi: {
      type: Boolean,
      default: true
    }
  },
  data () {
    const fakeValue = gridCheckValue++
    return {
      checkValue: '',
      fakeValue: fakeValue,
      opt: [{ value: fakeValue, label: '' }]
    }
  },
  watch: {
    checkValue (checkValue) {
      this.$emit('input', this.multi ? checkValue && checkValue.length > 0 : isDef(checkValue))
    },
    value (value) {
      if (value) {
        this.checkValue = this.multi ? [this.fakeValue] : this.fakeValue
      } else {
        this.checkValue = null
      }
    }
  }
}
</script>

