<template>
  <div class="ccb-pagination-jump">
    <span>前往</span>
    <ccb-input v-model = "jumpValue" @change="handleChange"></ccb-input>
    <span>页</span>
  </div>

</template>

<script>

import CcbInput from '@/ui/form/input/input'

export default {
  name: 'jump',
  components: {
    CcbInput
  },
  props: {
    totalPage: {
      type: Number,
      default: 0
    },
    currentPage: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {
      jumpValue: this.currentPage,
      oldValue: this.currentPage
    }
  },
  methods: {
    handleChange (evt) {
      const jumpPage = parseInt(evt.target.value, 10)
      if (this.isValidJump(jumpPage)) {
        this.oldValue = jumpPage
        this.$emit('jump-change', jumpPage)
      } else {
        this.jumpValue = this.oldValue
      }
    },
    isValidJump (value) {
      const total = this.totalPage
      if (value <= total && value > 0) {
        return true
      }
    }
  }
}
</script>

