<template>
  <div v-append-to-body class="ccb-loading" v-show="show">
    <ccb-icon class="ccb-loading-icon" name="loading"></ccb-icon>
    <p class="ccb-loading-text">{{text}}</p>
  </div>
</template>

<script>
import modalMixin from '@/mixin/modal-mixin'
import appendToBody from '@/directive/append-to-body'
import CcbIcon from '@/ui/basic/icon/icon'

export default {
  name: 'ccb-loading',
  components: {
    CcbIcon
  },
  directives: {
    appendToBody
  },
  mixins: [modalMixin],
  props: {
    show: {
      type: Boolean,
      default: false
    },
    text: {
      type: String,
      default: '正在加载中...'
    }
  },
  watch: {
    show: {
      immediate: true,
      handler (val) {
        if (val) {
          this.openModal(this)
        } else {
          this.closeModal(this)
        }
      }
    }
  }
}
</script>

