<template>
  <div v-click-outside="clickOutsideConfig" :class="['ccb-select',
    {
      'is-disabled': disabled
    }
  ]">
    <ccb-input v-model="inputValue" @click="clickHandler" @focus="focusHandler" :readonly="readonly" :disabled="disabled">
      <i slot="suffix" :class="['ccb-select-arrow','ccb-icon-arrow-down', {
        'is-chosing': this.optionVisible
      }]"></i>
    </ccb-input>
    <ccb-option v-model="selectValue" ref="optCmp" v-bind="$attrs" :visible.sync="optionVisible"></ccb-option>
  </div>
</template>

<script>

import CcbOption from '@/ui/popup/option/option'
import CcbInput from '@/ui/form/input/input'
import CcbIcon from '@/ui/basic/icon/icon'
import clickOutside from '@/directive/event-outside'
import { isDef } from '@/tools/util'

export default {
  name: 'ccb-select',
  components: {
    CcbOption,
    CcbInput,
    CcbIcon
  },
  directives: {
    clickOutside
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    value: {
      type: [Array, String, Number]
    }
  },
  computed: {
    readonly () {
      return !this.$attrs.filter && !this.$attrs.searchFn
    }
  },
  data () {
    return {
      clickOutsideConfig: {
        handler: this.clickOutsideHandler,
        otherEls: []
      },
      optionVisible: false,
      inputValue: '',
      selectValue: ''
    }
  },
  watch: {
    selectValue (val) {
      if (!val) return
      let emitVal
      // 填充到输入框里去, 将ccb-option返回值做改变，填充描述值到ccb-input, 将value值返回给v-model
      if (Array.isArray(val)) {
        emitVal = val.map(v => v.value)
        if (this.$attrs.cascade) {
          this.inputValue = val.map(v => v.label).join('/')
        } else {
          this.inputValue = val.map(v => v.label).join(',')
        }
      } else {
        emitVal = val.value
        this.inputValue = val.label
      }
      this.$emit('input', emitVal)
    }
  },
  methods: {
    focusHandler (e) {
      // this.optionVisible = true
    },
    clickHandler () {
      this.optionVisible = !this.optionVisible
    },
    clickOutsideHandler () {
      this.optionVisible = false
    }

  },
  mounted () {
    // 点击option也不会关闭option
    this.clickOutsideConfig.otherEls.push(this.$refs['optCmp'].$el)
  },
  created () {
    // 如果设置了初始值, TODO 待优化
    if (isDef(this.value)) {
      const val = this.value
      let selectValue
      if (Array.isArray(val)) {
        if (this.$attrs.cascade) {
          selectValue = []
          let i = 0
          let currentVal = val[i]
          let currentOptLevel = this.$attrs.options.find(opt => opt.value === currentVal.value)
          while (currentOptLevel) {
            selectValue.push(currentOptLevel)
            currentVal = val[++i]
            currentOptLevel = this.$attrs.options.find(opt => opt.value === currentVal.value)
          }
        } else {
          selectValue = val.map(v => this.$attrs.options.find(opt => opt.value === v))
        }
      } else {
        selectValue = this.$attrs.options.find(opt => opt.value === val)
      }
      this.selectValue = selectValue
    }
  }
}
</script>

