/**
 * 1. 用于统一管理modal遮罩窗口(其实modal dom全局就一个，但是要动态的维护z-index,确保z-index刚好比当前最上层的漂浮元素小1)
 * 2. 并且提供统一的zindex给所有需要的组件使用(比如window, tip, loading.. etc.)
 */

import { addClass } from '@/tools/dom-ops'

let zIndex = 1000 // 从1000开始递增

let modalStack = [] // 储存每一个modal信息，里面的属性应该是按照z-index递增
let modal

function getModal () {
  if (!modal) {
    modal = document.createElement('div')
    addClass(modal, 'ccb-modal')
    document.body.appendChild(modal)
  }
  return modal
}

function findModal (ins) {
  let existModal, idx
  for (let i = 0, len = modalStack.length; i < len; i++) {
    if (modalStack[i].ins === ins) {
      idx = i
      existModal = modalStack[i]
      break
    }
  }
  return { existModal, idx }
}

const popupManager = {

  openModal (ins) {
    let { existModal, idx } = findModal(ins)
    if (existModal) { // 这个实例的modal已经有了, 那么放到最顶层来
      existModal.zIndex = this.nextZIndex() // 更新zindex，确保在最上层
      modalStack.push(existModal)
      modalStack.splice(idx, 1)
    } else { // 还没有, 新增一个
      const modalDom = getModal()
      const zIndex = this.nextZIndex()
      modalDom.style.zIndex = zIndex
      modalDom.style.display = ''
      modalStack.push({ ins, zIndex })
    }
  },
  closeModal (ins) {
    if (modalStack.length > 0) { // 确保有modal
      const modalDom = getModal()
      if (ins) { // 指定了关闭某个modal
        let { existModal, idx } = findModal(ins)
        if (existModal) {
          if (idx === modalStack.length - 1 && idx > 0) { // 恰好是最上层, 且有至少两个modal, 那么得把z-index改一下了
            const secondModal = modalStack[idx - 1]
            modalDom.style.zIndex = secondModal.zIndex
          }
          modalStack.splice(idx, 1)
        }
      } else { // 默认关闭最上层的
        modalStack.pop()
        if (modalStack.length > 0) {
          modalDom.style.zIndex = modalStack[modalStack.length - 1].zIndex
        }
      }
      // 在关闭后, 假如没有modal了, 就把modal隐藏(之后还可能复用)
      if (modalStack.length === 0) {
        modalDom.style.display = 'none'
      }
    }
  },
  nextZIndex () {
    return zIndex++
  },
  currZIndex () {
    return zIndex
  }
}

export default popupManager