import loading from './loading'
import { mergeOpts } from '@/tools/util'

export default {
  install (Vue) {
    const Cmp = Vue.extend(loading)
    Cmp.prototype.close = function () {
      this.show = false
      this.$nextTick(() => {
        this.$destroy()
      })
    }
    Vue.prototype.$ccb.$loading = (options = {}) => {
      let ins = new Cmp()
      ins.$mount()
      options.show = true
      mergeOpts(ins, options)
      return ins
    }
  }
}