
export function flattenColumns (columns) {
  const rst = []
  columns.forEach(column => {
    rst.push(column)
    if (column.subColumns) {
      rst.push.apply(rst, flattenColumns(column.subColumns))
    }
  })
  return rst
}

/**
 * 把列信息根据层级关系重新组织，计算出colspan, rowspan等信息
 * @param {*} columns
 */
export function convertColumnsToHeaders (columns) {
  let maxLevel = 1

  // 计算colspan
  const dfs = function (column, parentColumn) {
    column.level = parentColumn ? (parentColumn.level + 1) : 1
    if (column.level > maxLevel) {
      maxLevel = column.level
    }
    if (column.subColumns) {
      let colspan = 0
      column.subColumns.forEach(subColumn => {
        dfs(subColumn, column)
        colspan += subColumn.colspan
      })
      column.colspan = colspan
    } else {
      column.colspan = 1
    }
  }

  columns.forEach(column => {
    dfs(column)
  })

  const covertedRows = []
  for (let i = 0; i < maxLevel; i++) {
    covertedRows.push([])
  }
  // 计算rowSpan, 然后按照层级顺序返回, 方便render编写
  const flattenedColumns = flattenColumns(columns)
  flattenedColumns.forEach(column => {
    column.rowspan = !column.subColumns ? (maxLevel - column.level + 1) : 1
    covertedRows[column.level - 1].push(column)
  })
  return covertedRows
}

const parseWidth = (width) => {
  if (width !== undefined) {
    width = parseInt(width, 10)
    if (isNaN(width)) {
      width = null
    }
  }
  return width
}

export function initColumn (config) {
  config.type = config.type || 'default'
  let column = Object.assign({}, defaultOptions[config.type], config)

  column.needRender = !!((column.field || column.type !== 'default'))
  column.width = parseWidth(column.width)
  column.minWidth = parseWidth(column.minWidth)
  column.realWidth = column.width ? column.width : column.minWidth
  let renderCell = column.renderCell
  column.renderCell = function (h, data) {
    if (!renderCell) {
      renderCell = renderFns[column.type].renderCell
    }
    let rst = renderCell.call(this, h, data)
    if (typeof (rst) === 'string') { // innerhtml
      return <div class="ccb-grid-cell" domPropsInnerHTML={rst}></div>
    } else {
      return <div class="ccb-grid-cell">{rst}</div>
    }
  }

  let renderHeader = column.renderHeader
  column.renderHeader = function (h, data) {
    if (!renderHeader) {
      renderHeader = renderFns[column.type].renderHeader
    }
    let rst = renderHeader.call(this, h, data)
    if (typeof (rst) === 'string') { // innerhtml
      return <div class="ccb-grid-cell" domPropsInnerHTML={rst}></div>
    } else {
      return <div class="ccb-grid-cell">{rst}</div>
    }
  }

  Object.assign(config, column)
  return config
}

export function initColumns (columns) {
  const flattenedColumns = flattenColumns(columns)
  flattenedColumns.forEach(column => {
    initColumn(column)
  })
  return columns
}

const defaultRenderCell = (h, { value }) => {
  return value
}

const defaultOptions = {
  'default': {
    minWidth: 100
  },
  'checkbox': {
    width: 50
  },
  'radio': {
    width: 50
  }
}

const renderFns = {
  'checkbox': {
    renderCell (h, { row, value, rowIdx }) {
      return <ccb-grid-check
        value={row.selected}
        on-input={(selected) => this.$ccbGridMsgCenter.$emit('selectRow', row, rowIdx, selected)}
      ></ccb-grid-check>
    },
    renderHeader (h, { value }) {
      return <ccb-grid-check
        value={this.selectAll}
        nativeOn-click={() =>
          this.$nextTick(() =>
            this.$ccbGridMsgCenter.$emit('toggleSelectAllRows', this.selectAll
            )
          )}
        on-input={(v) => { this.selectAll = v }}
      ></ccb-grid-check>
    }
  },
  'radio': {
    renderCell (h, { row, value, rowIdx }) {
      return <ccb-grid-check
        value={row.selected}
        multi={false}
        nativeOn-click={() => this.$ccbGridMsgCenter.$emit('selectSingleRow', row, rowIdx)}
      ></ccb-grid-check>
    },
    renderHeader (h) {
      return <div></div>
    }
  },
  'default': {
    renderCell: defaultRenderCell,
    renderHeader: defaultRenderCell
  }
}