<template>
  <div v-click-outside="clickOutsideConfig" :class="['ccb-date',
    {
      'is-disabled': disabled
    }
  ]">
    <ccb-input ref="inputRef" v-model="inputValue" @click="clickHandler" readonly :disabled="disabled">
      <i slot="suffix" :class="['ccb-select-arrow','ccb-icon-date', {
        'is-chosing': this.optionVisible
      }]"></i>
    </ccb-input>
    <ccb-date-option
      v-model="dateValue"
      ref="optCmp" v-bind="$attrs"
      :visible.sync="optionVisible">
    </ccb-date-option>
  </div>
</template>

<script>
import CcbDateOption from '@/ui/popup/option/date-option'
import CcbInput from '@/ui/form/input/input'
import CcbIcon from '@/ui/basic/icon/icon'
import clickOutside from '@/directive/event-outside'
import { formatDate } from '@/tools/date'

export default {
  name: 'ccb-date',
  components: {
    CcbDateOption,
    CcbInput,
    CcbIcon
  },
  directives: {
    clickOutside
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    value: {
      type: String
    },
    displayFormat: {
      type: String,
      default: 'yyyyMMdd'
    },
    valueFormat: {
      type: String,
      default: 'yyyyMMdd'
    }
  },
  data () {
    return {
      clickOutsideConfig: {
        handler: this.clickOutsideHandler,
        otherEls: []
      },
      optionVisible: false,
      inputValue: '',
      dateValue: new Date()
    }
  },
  watch: {
    dateValue (date) {
      this.inputValue = formatDate(date, this.displayFormat)
      this.$emit('input', formatDate(date, this.valueFormat))
    }
  },
  methods: {
    clickHandler () {
      this.optionVisible = !this.optionVisible
    },
    clickOutsideHandler () {
      this.optionVisible = false
    }
  },
  mounted () {
    // 点击option也不会关闭option
    this.clickOutsideConfig.otherEls.push(this.$refs['optCmp'].$el)
  }
}
</script>

