<script>

import CcbIcon from '@/ui/basic/icon/icon'

export default {
  components: {
    CcbIcon
  },
  name: 'ccb-progress-bar',
  props: {
    progress: {
      type: Number,
      default: 0,
      validator: v => v >= 0 && v <= 100
    },
    status: {
      type: String,
      default: 'loading',
      validator: v => ['loading', 'success', 'error'].indexOf(v) >= 0
    },
    barWidth: {
      type: [String, Number]
    },
    barHeight: {
      type: [String, Number]
    }
  },
  computed: {
    barMsg () {
      if (this.status === 'loading') {
        return this.progress + '%'
      } else if (this.status === 'success') {
        return '成功'
      } else {
        return '失败'
      }
    },
    innerStyle () {
      return {
        width: this.progress + '%'
      }
    }
  },
  data () {
    return {

    }
  },
  render () {
    let myslot = <span>{this.barMsg}</span>
    /* if (this.status === 'loading') {
      myslot = (
        <span>{this.loadingMsg}</span>
      )
    } else if (this.status === 'success') { // TODO 等着图标
      myslot = <ccb-icon name = "jiantouxia"></ccb-icon>
    } else {
      myslot = <ccb-icon name = "Shapex"></ccb-icon>
    } */
    let data = {
      class: ['is-' + (this.progress === 100 ? 'success' : this.status)] // 达到100后自动改为success展示, 那么...status是不是该撸成v-model或者.sync
    }
    let outerData = {
      style: { height: this.barHeight + 'px' }
    }
    return (
      <div class="ccb-progressbar" {...data}>
        <div class="ccb-progressbar-outer" {...outerData}>
          <div class="ccb-progressbar-inner" style={this.innerStyle} >
          </div>
        </div>
        <span class="ccb-progressbar-text">
          {this.$slots[this.status] ? this.$slots[this.status] : myslot}
        </span>
      </div>
    )
  }
}
</script>

