import ResizeObserver from 'resize-observer-polyfill'

const map = Object.create(null)

const resizeListener = function (entries) {
  for (const entry of entries) {
    const callbacks = map[entry.target]
    for (const cb of callbacks) {
      cb(entry)
    }
  }
}

export function addResizeListener (el, callback) {
  if (!map[el]) {
    map[el] = []
    map[el]._ro = new ResizeObserver(resizeListener)
    map[el]._ro.observe(el)
  }
  map[el].push(callback)
}

export function removeResizeListener (el, callback) {
  if (map[el]) {
    if (callback) {
      const idx = map[el].indexOf(callback)
      if (idx > -1) {
        map[el].splice(idx, 1)
      }
    } else {
      map[el].length = 0
    }
    if (map[el].length === 0) {
      map[el]._ro.disconnect()
      map[el] = undefined
    }
  }
}