<template>
  <div :class="['ccb-row',
    'ccb-row-' + type
    ]"
    :style="styles">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'CcbRow',
  props: {
    gutter: {
      type: Number,
      default: 0
    },
    justify: String,
    align: {
      type: String
    },
    wrap: {
      type: String,
      default: 'wrap'
    },
    direction: String,
    type: {
      type: String,
      default: 'flex',
      validator: v => ['flex', 'table'].indexOf(v) > -1
    }
  },
  computed: {
    styles () {
      const styles = {
        'justify-content': this.justify,
        '-webkit-justify-content': this.justify,
        'align-items': this.align,
        '-webkit-align-items': this.align,
        'flex-wrap': this.wrap,
        '-webkit-flex-wrap': this.wrap,
        'flex-direction': this.direction,
        '-webkit-flex-direction': this.direction,
        'marginLeft': this.gutter ? -this.gutter / 2 + 'px' : '',
        'margin-right': this.gutter ? -this.gutter / 2 + 'px' : ''
      }
      return styles
    }
  }
}
</script>
