<template>
  <div
    :class="['ccb-col',
      'ccb-col-' + span,
      responsiveClass
    ]"
    :style="style">
    <slot></slot>
  </div>
</template>

<script>
/* const prefixList = ['-moz-box-', '-webkit-box-', ''] */

import { isDef, isObject } from '@/tools/util'

export default {
  name: 'CcbFlexItem',
  props: {
    span: {
      type: Number,
      validator: v => v >= 0 && v <= 24 && Math.floor(v) === v
    },
    xxs: [Number, Object],
    xs: [Number, Object],
    sm: [Number, Object],
    md: [Number, Object],
    lg: [Number, Object],
    xl: [Number, Object],
    xxl: [Number, Object],
    order: Number
  },
  beforeMount () {
    this.bodyWidth = document.documentElement.offsetWidth
  },
  methods: {
    buildWidth (width) {
      if (typeof width === 'number') {
        if (width < 1) {
          return width
        } else {
          return width / 12
        }
      } else if (typeof width === 'string') {
        return width.replace('px', '') / this.bodyWidth
      }
    }
  },
  computed: {
    style () {
      let styles = {}
      /*
      * 考虑设置了warp的情况下, 比如当要把均分4个50%的flex-item，但是有20px的间隙, 如何做到 ?
      * 1. 为flex容器设置负margin(如margin: 0 -10px), 给flex-item设置二分之一的padding(如padding: 0 10px)值, 但是这样会给flex扩大本身的大小
      * 2. 采用calc表达式, 比如设置flex属性为0 0 calc(50% - 10px)...但是这种方法, 还需要知道从哪一个flex-item开始换行了
      * 先采用第一种姿势(第二种有点麻烦)
      */
      styles['padding'] = `0 ${this.$parent.gutter / 2}px`
      // flex order
      if (typeof this.order !== 'undefined') {
        styles.order = this.order
      }
      return styles
    },
    responsiveClass () {
      const classes = []
      ;['xxs', 'xs', 'sm', 'md', 'lg', 'xl', 'xxl'].forEach(size => {
        if (isDef(this[size])) {
          if (isObject(this[size])) {
            // 这种情况允许定义在某个size下的所有其他COL props(都是通过class来定义的)
            const props = this[size]
            Object.keys[props].forEach(propName => {
              classes.push(`ccb-col-${size}${propName === 'span' ? '' : '-' + propName}-${this[size]}`)
            })
          } else {
            classes.push(`ccb-col-${size}-${this[size]}`)
          }
        }
      })
      return classes
    }
  },
  data () {
    return {
      bodyWidth: 0,
      isNewLine: false
    }
  },
  mounted () {
  }
}
</script>
