<template>
  <ccb-select v-model="selectValue" class="ccb-pagination-size" :options="selOptions"></ccb-select>
</template>

<script>
import CcbSelect from '@/ui/form/select/select'

export default {
  name: 'size',
  components: {
    CcbSelect
  },
  props: {
    options: Array,
    value: Number
  },
  data () {
    return {
      selectValue: this.value
    }
  },
  watch: {
    selectValue (v) {
      this.$emit('input', v)
    }
  },
  computed: {
    selOptions () {
      return this.options.map(opt => ({ label: `${opt}条/页`, value: opt }))
    }
  }
}
</script>

