<template>
  <div class="ccb-check">
    <div
    v-for="(opt,idx) of opts"
    :class="['ccb-check-item', {
      'is-disabled': disabled || opt.disabled,
      'is-checked': opt.checked
    }]"
    :key="opt.value ? opt.value + idx : opt + idx"
    @click="clickCheck(idx)">
      <i :class="['ccb-check-icon', 'ccb-check-icon-' + (multi ? 'checkbox' : 'radio')]">
      </i>
      <span>{{opt.label}}</span>
    </div>
  </div>
</template>

<script>
import { isObject, isDef, deepClone } from '@/tools/util'

function checkOpts (value, opts) {
  value = Array.isArray(value) ? value : [value]
  opts.forEach(opt => {
    opt.checked = value.some(v => {
      return v === opt.value
    })
  })
}

function validateOpts (opts) {
  if (opts.length === 0) {
    console.error('ccb-check prop options不应该是一个空数组')
    return false
  }
  if (isObject(opts[0])) {
    if (!opts.every(ele => isObject(ele))) {
      console.error('ccb-check prop options数组元素应该全为对象或者全为字符串')
      return false
    }
    if (!opts.every(ele => isDef(ele.label) && isDef(ele.value))) {
      console.error('ccb-check prop options数组元素对象应该包含label和value')
      return false
    }
  } else {
    if (!opts.every(ele => typeof (ele) === 'string')) {
      console.error('ccb-check prop options数组元素应该全为对象或者全为字符串')
      return false
    }
  }
  return true
}

function normalizOpts (opts) {
  if (!isObject(opts[0])) { // 若是字符串形式的选项
    const len = opts.length
    for (var i = 0; i < len; i++) {
      opts.push({ label: opts[i], value: opts[i] })
    }
    opts.splice(0, len)
  }
  opts.forEach(opt => {
    opt.checked = opt.checked || false
    opt.disabled = opt.disabled || false
  })
  return opts
}

function normalizValue (opts) {
  let value = []
  /* if (isObject(opts[0])) {
    value = opts.filter(opt => opt.checked).map(opt => ({ value: opt.value, label: opt.label })) // 不要把checked属性给返回了
  } else {
    value = opts.filter(opt => opt.checked).map(opt => opt.value)
  } */
  value = opts.filter(opt => opt.checked).map(opt => opt.value)
  if (value.length === 0) { // 没选中
    value = null
  } else if (!this.multi) { // 单选就返回字符串
    value = value[0]
  }
  return value
}

export default {
  name: 'ccb-check',
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    multi: {
      type: Boolean,
      default: true
    },
    value: {
      default: null
    },
    options: {
      type: Array,
      required: true,
      validator: opts => validateOpts(opts)
    }
  },
  data () {
    return {
      opts: normalizOpts(deepClone(this.options)) // 我们会对options做修改, 为了不修改调用者传入的对象, 深度克隆一份
    }
  },
  /* created () {
    // 根据v-model初始值, 配置选中
    checkOpts(this.value, this.opts)
  }, */
  watch: {
    value: {
      immediate: true,
      handler (value) {
        checkOpts(value, this.opts)
      }
    }
  },
  methods: {
    clickCheck (idx) {
      let checkedItem = this.opts[idx]
      if (checkedItem.disabled || this.disabled) { // 禁用状态，直接返回
        return
      }
      // 单选的话，先把其他选项取消
      if (!this.multi) {
        this.opts.forEach(opt => {
          if (opt !== checkedItem) {
            opt.checked = false
          }
        })
      }

      checkedItem.checked = !checkedItem.checked // 修改选中状态

      // 更改v-model值
      const value = normalizValue.call(this, this.opts)
      this.$emit('change', value)
      this.$emit('input', value)
    }
  }
}
</script>
