<template>
  <div class="ccb-grid"
    :style="[gridStyle]"
  >
    <div class="ccb-grid-header"
      ref="headerWrapper"
    >
      <ccb-grid-header ref="header"
        :column-rows="headerRows" :columns="flattenedColumns"
        :style="[headerStyle]"
      >
      </ccb-grid-header>
    </div>
    <div class="ccb-grid-body"
      ref="bodyWrapper"
      :style="[bodyWrapperStyle]"
    >
      <ccb-grid-body
        :data="internalData" :columns="flattenedColumns"
        :style="[bodyStyle]"
      >
      </ccb-grid-body>
    </div>
  </div>
</template>

<script>
import CcbGridBody from './body'
import CcbGridHeader from './header'
import { noop, deepClone } from '@/tools/util'
import { parseUnit } from '@/tools/css-ops'
import { addResizeListener, removeResizeListener } from './resize-observer'
import * as util from './util'
import Vue from 'vue'

export default {
  name: 'ccb-grid',
  components: {
    CcbGridBody,
    CcbGridHeader
  },
  provide () {
    return {
      '$ccbGridMsgCenter': this.msgCenter
    }
  },
  props: {
    width: {
      type: [String, Number],
      default: '100%'
    },
    height: {
      type: [String, Number],
      default: 'auto'
    },
    maxHeight: {
      type: [String, Number]
    },
    columnData: {
      type: Array,
      default: () => []
    },
    noDataText: {
      type: String,
      default: '尊敬的用户, 查询数据为空'
    },
    rowStyler: {
      type: Function,
      default: noop
    },
    cellStyler: {
      type: Function,
      default: noop
    },
    headerStyler: {
      type: Function,
      default: noop
    },
    merge: {
      type: Function,
      default: noop
    },
    columns: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    headerRows () {
      return util.convertColumnsToHeaders(this.internalColumns)
    },
    flattenedColumns () {
      return util.flattenColumns(this.internalColumns)
    },
    gridStyle () {
      return {
        width: parseUnit(this.width)
      }
    },
    bodyStyle () {
      return {
        width: parseUnit(this.bodyWidth)
      }
    },
    bodyWrapperStyle () {
      return {
        height: parseUnit(this.height),
        'max-height': parseUnit(this.maxHeight)
      }
    },
    headerStyle () {
      return {
        width: parseUnit(this.bodyWidth)
      }
    }
  },
  data () {
    return {
      internalColumns: [],
      internalData: [],
      msgCenter: new Vue(),
      lastSizeState: Object.create(null),
      columnConfigs: [],
      bodyWidth: ''
    }
  },
  watch: {
    columns: {
      immediate: true,
      handler (newColumns) {
        const initColumns = util.initColumns(deepClone(this.columns))
        this.internalColumns = initColumns
        this.updateColumnsWidth()
      }
    },
    columnData: {
      immediate: true,
      deep: false,
      handler (newData) {
        const copy = deepClone(newData)
        this.internalData.length = 0
        copy.forEach(row => {
          this.internalData.push({
            selected: false,
            data: row
          })
        })
      }
    }
  },
  created () {
    if (!this.$slots.default && this.columns) {

    }
  },
  mounted () {
    addResizeListener(this.$el, this.resizeListener)
    const { headerWrapper, bodyWrapper } = this.$refs
    bodyWrapper.addEventListener('scroll', function () {
      const scrollLeft = this.scrollLeft
      headerWrapper.scrollLeft = scrollLeft
    })
    const self = this
    this.msgCenter.$on('selectRow', (row, rowIdx, selected) => {
      self.internalData[rowIdx].selected = selected
      if (!selected) {
        self.$refs['header'].selectAll = false
      }
    })
    this.msgCenter.$on('toggleSelectAllRows', selectAll => {
      self.internalData.forEach(row => {
        row.selected = selectAll
      })
    })
    this.msgCenter.$on('selectSingleRow', (row, rowIdx) => {
      self.internalData.forEach(_row => {
        _row.selected = row === _row ? !row.selected : false
      })
    })
  },
  destroyed () {
    removeResizeListener(this.$el)
  },
  methods: {
    resizeListener (entry) {
      const { width, height } = entry.contentRect
      const { width: oldWidth, height: oldHeight } = this.lastSizeState
      let shouldResize = false
      if (width !== oldWidth) {
        this.lastSizeState.width = width
        shouldResize = true
      }
      if (height !== oldHeight) {
        // 之后fix column的情况需要用到
      }
      if (shouldResize) {
        this.resize()
      }
    },
    resize () {
      this.updateColumnsWidth()
    },
    /**
     * 列宽规则:
     * 1. 若设置了width, 则保证width宽度
     * 2. 若设置了minWidth, 则保证minWidth宽度, 当宽度有剩余时, 会额外分配
     * 3. 若没有设置width & minWidth, 当宽度有剩余时，会额外分配，否则按照最小100px
     */
    updateColumnsWidth () {
      // ssr的情况下, 直接返回
      if (Vue.prototype.$isServer) {
        return
      }
      // 注意这里可能在ssr的情况下导致死循环
      if (!this.$el) {
        this.$nextTick(() => this.updateColumnsWidth())
        return
      }
      const flattened = util.flattenColumns(this.internalColumns)
      const gridWidth = this.$el.clientWidth
      let gridMinWidth = 0
      let denominator = 0
      flattened.forEach(column => {
        if (column.colspan === 1) {
          gridMinWidth += column.width || column.minWidth
          if (!column.width) {
            denominator += column.minWidth
          }
        }
      })
      // 没有设置width的列要重新分配宽度
      if (gridMinWidth < gridWidth) {
        const remainedWidth = gridWidth - gridMinWidth // 剩余可分配的
        let actualWidth = 0 // 实际分配之和（因为会做四舍五入，避免小数点)
        let chosenColumn // 用来分配因为四舍五入导致丢失的宽度的列（就选第一列)
        flattened.forEach(column => {
          if (column.colspan === 1 && !column.width) {
            chosenColumn = chosenColumn || column
            const addtionalWidth = Math.floor(column.minWidth / denominator * remainedWidth)
            actualWidth += addtionalWidth
            column.realWidth = column.minWidth + addtionalWidth
          }
        })
        if (actualWidth !== remainedWidth && chosenColumn) {
          chosenColumn.realWidth += remainedWidth - actualWidth
        }
      } else { // 本身宽度不够,那么给header、body扩大宽度(左右滑动滚动)
        this.bodyWidth = gridMinWidth
      }
    },
    getSelections () {
      return this.internalData.filter(row => row.selected).map(row => row.data)
    }
  }
}
</script>

