/**
 * 将弹出框定位到某个元素附近, 比如下拉选项、悬浮提示等
 * 1. 监听resize, scroll等动态调整位置
 * 2. 根据当前位置, 调整向上或向下等展示位置
 * 3. 动态设置z-index
 */

import modalManager from './modal-manager'
import Popup from './popup'

export default {
  mounted () {

  },
  methods: {
    $ccb_initPopup (el, popupEl, opts = {}) {
      this.$ccb_popup = new Popup(el, popupEl, opts)
      this.$ccb_popup.popupEl.style.zIndex = modalManager.nextZIndex()
    },
    // 重新打开时应该调用该方法
    $ccb_updatePopup () {
      if (this.$ccb_popup) {
        this.$ccb_popup.popupEl.style.zIndex = modalManager.nextZIndex()
        this.$ccb_popup.updatePopup()
      }
    },
    // destroy
    $ccb_destroyPopup () {
      this.$ccb_popup.destroy()
    }
  }
}