<template>
  <ccb-window :show.sync="show" :title="title" :destroyWhenClose="destroyWhenClose" width="400px">
    <div class="ccb-messagebox-content">
      <ccb-icon v-if="styleType" :name="iconName" :class="['ccb-messagebox-' + styleType]"></ccb-icon>
      <p>{{content}}</p>
    </div>
    <div slot="footer">
      <ccb-button size="small" @click="handleCloseBox('Ok')">确定</ccb-button>
      <template v-if="type === 'confirm'">
        <ccb-button size="small" @click="handleCloseBox('Cancel')" type="blank">取消</ccb-button>
      </template>
    </div>
  </ccb-window>
</template>

<script>

import CcbWindow from '@/ui/popup/window/window'
import CcbButton from '@/ui/basic/button/button'
import CcbIcon from '@/ui/basic/icon/icon'
import { noop } from '@/tools/util'

const styleMapping = {
  info: 'info',
  success: 'success',
  error: 'error',
  warning: 'warning'
}

export default {
  name: 'ccb-messagebox',
  components: {
    CcbWindow,
    CcbButton,
    CcbIcon
  },
  props: {
    type: {
      type: String,
      validator: v => ['alert', 'confirm'].indexOf(v) > -1,
      default: 'alert'
    },
    title: {
      type: String
    },
    styleType: { // 其实就是默认在内容区前面添加个icon
      type: String,
      validator: v => ['info', 'warning', 'error', 'success'].indexOf(v) > -1
    },
    content: {
      type: String
    },
    onOk: {
      type: Function,
      default: noop
    },
    onCancel: {
      type: Function,
      default: noop
    },
    onClose: {
      type: Function,
      default: noop
    },
    show: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      // show: true,
      destroyWhenClose: false
    }
  },
  computed: {
    iconName () {
      return styleMapping[this.styleType]
    }
  },
  watch: {
    show (val) {
      if (val === false && !this._closed) {
        this.onClose()
      }
    }
  },
  methods: {
    handleCloseBox (action) {
      this._closed = true // 避免show再次触发onClose回调
      this.show = false
      this['on' + action]()
    }
  },
  beforeDestroy () {
    console.log('before destroy')
  }
  /* render () {
    let footerSlot
    if (this.type === 'alert') {
      footerSlot = <div slot="footer"><ccb-button>确定</ccb-button></div>
    } else if (this.type === 'confirm') {
      footerSlot = <div slot="footer"><ccb-button>确定</ccb-button><ccb-button type="blank">取消</ccb-button></div>
    }
    return (
      <div>
      <ccb-window show="show" {...{on: {'update:show': a}}}>
        {footerSlot}
      </ccb-window>
      </div>
    )
  } */
}
</script>

