/**
 * 点击元素区域之外触发
 */
const defaultEvents = ['click'] // 默认的关注事件(点击元素之外的地方触发事件)

const instances = [] // 储存el和注册事件

const normalizeBinding = (value) => {
  const isFn = typeof (value) === 'function'
  return {
    handler: isFn ? value : value.handler,
    events: isFn ? defaultEvents : (value.events || defaultEvents),
    otherEls: isFn ? [] : (value.otherEls || []) // 额外的DOM, 考虑到下拉框这种类似的情况
  }
}

// 事件是否触发在元素之外
const isOutside = (el, e) => !el || (el !== e.target && !el.contains(e.target))

/**
 * @param {*} events 事件名数组
 * @param {*} el 元素dom
 * @param {*} handler 回调
 */
const genHandlers = (events, el, otherEls, vnode, handler) => {
  return events.map(event => ({
    event,
    handler: e => {
      // 点击绑定元素自身
      if (!isOutside(el, e)) {
        return
      }
      // 点击其他例外的元素，比如弹出选择框(挂载在BODY下)
      // att. [].every(()=>{}) 返回true...
      if (otherEls.length > 0 && otherEls.every(otherEl => !isOutside(otherEl, e))) {
        return
      }
      handler(e)
    }
  }))
}

const registerInsHandler = ins => {
  ins.handlers.forEach(({ event, handler }) => {
    document.addEventListener(event, handler) // 万一有人瞎比阻止冒泡，是否考虑在捕获阶段?
  })
}

const removeInsHandler = ins => {
  ins.handlers.forEach(({ event, handler }) => {
    document.removeEventListener(event, handler)
  })
}

export default {
  bind (el, binding, vnode) {
    const { handler, events, otherEls } = normalizeBinding(binding.value)
    const ins = {
      el,
      handlers: genHandlers(events, el, otherEls, vnode, handler)
    }
    instances.push(ins)
    registerInsHandler(ins)
  },
  update (el, binding, vnode) { // 删除了重新注册，可能有更改
    const ins = instances.find(ins => ins.el === el)
    removeInsHandler(ins)
    const { handler, events, otherEls } = normalizeBinding(binding.value)
    ins.handlers = genHandlers(events, el, otherEls, vnode, handler)
    registerInsHandler(ins)
  },
  unbind (el, binding) {
    const ins = instances.find(ins => ins.el === el)
    removeInsHandler(ins)
    const idx = instances.indexOf(ins)
    instances.splice(idx, 1)
  }
}