<script>

import CcbIcon from '@/ui/basic/icon/icon'
import { hasClass } from '@/tools/dom-ops'

export default {
  name: 'page',
  components: {
    CcbIcon
  },
  props: {
    maxDisplay: { // 最多展示数字的个数
      type: Number,
      default: 7
    },
    currentPage: {
      type: Number,
      default: 1
    },
    totalPage: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      leftMore: false,
      rightMore: false
    }
  },
  methods: {
    handleClickPage ({ target }) {
      let newPageNumber
      // 点更多的话，在当前基础上下翻maxDisplay-2个数
      const offset = this.maxDisplay - 2
      if (hasClass(target, 'ccb-pagination-premore') ||
          hasClass(target, 'ccb-icon-double-arrow-left')
      ) {
        newPageNumber = this.currentPage - offset
      } else if (hasClass(target, 'ccb-pagination-nextmore') ||
          hasClass(target, 'ccb-icon-double-arrow-right')
      ) {
        newPageNumber = this.currentPage + offset
      } else { // 点的数字
        newPageNumber = Number(target.textContent) || 1 // 不会NaN吧？以防万一
      }
      // 修正下可能的边界情况
      if (newPageNumber < 1) {
        newPageNumber = 1
      } else if (newPageNumber > this.totalPage) {
        newPageNumber = this.totalPage
      }
      // 有改变, emit事件
      if (newPageNumber !== this.currentPage) {
        this.$emit('page-change', newPageNumber)
      }
    }
  },
  render () {
    const container = <div onClick={this.handleClickPage} class="ccb-pagination-page"></div>
    if (this.totalPage < 0) {
      return container
    }
    const left = Math.floor(this.maxDisplay / 2) // 左边显示个数
    const right = this.maxDisplay - left - 1 // 右边显示个数
    const leftEllipsis = this.currentPage > left // 是否显示左边省略号
    const rightEllipsis = (this.currentPage + right) < this.totalPage // 是否显示右边省略号
    let middleStartNumber // 中间部分的起始数字
    let middleEndNumber // 中间部分的尾部数字
    if (leftEllipsis && rightEllipsis) {
      middleStartNumber = this.currentPage - left + 1
      middleEndNumber = this.currentPage + right - 1
    } else if (!leftEllipsis && !rightEllipsis) {
      middleStartNumber = 1
      middleEndNumber = this.totalPage
    } else if (leftEllipsis && !rightEllipsis) {
      middleStartNumber = this.totalPage - this.maxDisplay + 2
      middleEndNumber = this.totalPage
    } else {
      middleStartNumber = 1
      middleEndNumber = this.maxDisplay - 1
    }
    const middleNumbers = [...Array(middleEndNumber - middleStartNumber + 1).keys()].map(i => i + middleStartNumber) // 中间显示的数字
    const children = container.children = []
    if (leftEllipsis) {
      children.push(<div>1</div>)
      children.push(<div
        onMouseenter={() => { this.leftMore = true }}
        onMouseleave={() => { this.leftMore = false }}
        class = "ccb-pagination-premore">
        <ccb-icon name={this.leftMore ? 'double-arrow-left' : 'ellipsis'}></ccb-icon>
      </div>)
    }
    middleNumbers.forEach(number => {
      children.push(<div class={{ 'is-active': number === this.currentPage }}>{number}</div>)
    })
    if (rightEllipsis) {
      children.push(<div
        onMouseenter={() => { this.rightMore = true }}
        onMouseleave={() => { this.rightMore = false }}
        class = "ccb-pagination-nextmore">
        <ccb-icon name={this.rightMore ? 'double-arrow-right' : 'ellipsis'}></ccb-icon>
      </div>)
      children.push(<div>{this.totalPage}</div>)
    }
    return container
  }
}
</script>

