
<script>
import { CcbRow, CcbCol } from '@/ui/layout'
import Schema from 'async-validator'
import { noop, getValueByPath, jsxClasses } from '@/tools/util'
import eventMixin from '@/mixin/event-mixin'

export default {
  name: 'ccb-form-item',
  components: {
    CcbRow,
    CcbCol
  },
  inject: ['ccbForm'],
  mixins: [eventMixin],
  props: {
    label: {
      type: String
    },
    labelCol: {
      type: Object
      // default: () => ({ span: 5 })
    },
    itemCol: {
      type: Object
      // default: () => ({ span: 12 })
    },
    rules: {
      type: Array,
      default: () => []
    },
    field: {
      type: String
    },
    required: {
      type: Boolean
    }
  },
  computed: {
    // 需要查看rules里面是否已经有required
    isRequired () {
      return this.required || this.allRules.some(rule => rule.required)
    },
    // 组件上配置的required会增加一条rule
    allRules () {
      const rules = this.rules.slice()
      return this.required ? rules.concat({ required: true }) : rules
    },
    // 表单组件的值, field属性应该和配置在表单组件上的v-model属性一致!
    itemValue () {
      const model = this.ccbForm.model
      return getValueByPath(model, this.field, true).v
    }
  },
  data () {
    return {
      state: '', // 验证状态pending, error, success
      tipMsg: '', // 验证的提示信息
      showTip: false // 这玩意需要在监听动画的时候来设置, 因为tip的有无关系到margin-bottom
    }
  },
  render () {
    const children = [this.renderLabel(), this.renderItem()]
    const cls = jsxClasses('ccb-form-item', {
      'ccb-form-item-withtip': this.showTip
    })
    return (
      <ccb-row type="table" class={cls}>
        {children}
      </ccb-row>
    )
  },
  mounted () {
    if (this.field) {
      // 添加引用到ccb-form中去
      this.$ccb_bubble('ccb-form', 'ccb-form-addItem', this)
      // 设置初始化值, 为了之后重置表单使用
      const initValue = this.itemValue
      Object.defineProperty(this, 'initValue', {
        value: initValue,
        enumerable: false,
        writable: false,
        configurable: false
      })
      // 用于配置了trigger的rule触发(表单元素应该要自己实现事件的$emit)
      this.$on('ccb-form-item-blur', this.onItemBlur)
      this.$on('ccb-form-item-input', this.onItemChange)
      this.$on('ccb-form-item-change', this.onItemInput)
    }
  },
  beforeDestroy () {
    this.$ccb_bubble('ccb-form', 'ccb-form-removeItem', this)
  },
  methods: {
    validate (cb = noop, triggerType) {
      this.state = 'pending'
      let filteredRules = this.allRules
      // 获取符合triggerType的rule
      if (triggerType) {
        filteredRules = this.allRules.filter(rule => rule.trigger && rule.trigger.indexOf(triggerType) > -1)
      }
      // 创建async-validator
      const descriptor = {}
      descriptor[this.field] = filteredRules
      const validator = new Schema(descriptor)
      const source = {}
      source[this.field] = this.itemValue
      validator.validate(source, { firstFields: true }, (errs, errItems) => {
        this.state = errs ? 'error' : 'success'
        this.tipMsg = errs ? errs[0].message : ''
        cb(errs, errItems)
      })
    },
    resetItem () {
      this.clearValidate()
      const model = this.ccbForm.model
      const field = getValueByPath(model, this.field, true)
      // 改变v-model, 回到初始值
      field.o[field.k] = this.initValue
    },
    clearValidate () {
      this.state = ''
      this.tipMsg = ''
    },
    getDefaultColConfig () {
      if (this.ccbForm.layout === 'horizontal') {
        return {
          label: { span: 6 },
          item: { span: 16 }
        }
      } else {
        return { 
          label: {},
          item: {}
        }
      }
    },
    renderItem () {
      const { itemCol = this.getDefaultColConfig().item } = this
      const { class: cls, style, ...restProps } = itemCol
      const itemProps = {
        class: jsxClasses('ccb-form-item-input', cls),
        style,
        props: restProps
      }

      let wrapperClass = jsxClasses('ccb-form-item-wrapper', {
        'is-pending': this.state === 'pending',
        'is-error': this.state === 'error',
        'is-success': this.state === 'success'
      })
      const tip = this.tipMsg
        ? <div class="ccb-form-item-validateTip">{this.tipMsg}</div>
        : null
      if (tip) {
        this.showTip = true
      }
      return (
        <ccb-col {...itemProps}>
          <div class={wrapperClass}>
            {this.$slots.default}
            <transition
              onAfterEnter={() => { this.showTip = true }}
              onAfterLeave={() => { this.showTip = false }}
              name="ccb-zoom-in-top">
              {tip}
            >
            </transition>
          </div>
        </ccb-col>
      )
    },
    renderLabel () {
      if (!this.label) {
        return null
      }
      const { labelCol = this.getDefaultColConfig().label } = this
      const { class: cls, style, ...restProps } = labelCol
      const itemProps = {
        class: jsxClasses('ccb-form-item-label', cls),
        style,
        props: restProps
      }
      const labelCls = jsxClasses({
        'is-required': this.isRequired
      })
      return (
        <ccb-col {...itemProps}>
          <label class={labelCls}>{this.label}</label>
        </ccb-col>
      )
    }
  }
}
</script>
