/**
 * 只有ui
 */

import * as ui from '@/ui'
import plugin from '@/plugin'
import '@/assets/css/app.scss'
import { version } from '../../package.json'

const install = function (Vue) {
  Object.keys(ui).forEach(uiName => {
    Vue.component(uiName, ui[uiName])
  })
  Vue.use(plugin)
}

export default {
  version,
  install
}

export * from '@/ui'