<template>
  <div class="ccb-upload">
    <template v-if="type === 'file'">
      <div class="ccb-upload-choose">
        <div class="ccb-upload-filebutton" @click="handleClick">
          <slot name="button">
            <ccb-button icon="add">添加附件</ccb-button>
          </slot>
        </div>
        <ul class="ccb-upload-filelist">
          <template v-for="(file) of fileList">
            <li :key="file.uid" class="ccb-upload-file">
              <div class="ccb-upload-bar">
                <div>{{file.name}}</div>
                <ccb-progress-bar ref="bar" :status="file.status" :progress="file.progress">
                </ccb-progress-bar>
              </div>
              <span class="ccb-upload-op">
                <ccb-icon name="shuaxin" v-if="file.error" @click="reUpload(file)"></ccb-icon>
                <ccb-icon name="guanbi" @click="abortUpload(file)"></ccb-icon>
              </span>
           </li>
          </template>

        </ul>
      </div>
    </template>
    <template v-if="type === 'picture'">
      <div class="ccb-upload-picturebutton" @click="handleClick">

      </div>
    </template>
    <input ref = "fileInput" type="file" :name="name" style="display:none" @change="handleFileChange" :accept="accept" :multiple="multi"/>
  </div>
</template>

<script>

// 每一个上传的uu id

import CcbButton from '@/ui/basic/button/button'
import CcbProgressBar from '@/ui/data/progress-bar/progress-bar'
import CcbIcon from '@/ui/basic/icon/icon'
import { httpReq } from '@/network/req'
import { noop } from '@/tools/util'

let uploadingUid = 0

function normalizeFiles (files) {
  return Array.prototype.map.call(files, f => {
    const { name, size, type } = f
    return {
      name,
      size,
      type,
      uid: uploadingUid++,
      // progress-bar 状态
      progress: 0,
      status: 'loading',
      rawFile: f
    }
  })
}

export default {
  name: 'ccb-upload',
  components: {
    CcbButton,
    CcbProgressBar,
    CcbIcon
  },
  props: {
    type: {
      type: String,
      default: 'file',
      validator: v => ['file', 'picture'].indexOf(v) >= 0
    },
    url: {
      required: true,
      type: String
    },
    accept: {
      type: String
    },
    name: {
      type: String,
      default: 'file'
    },
    multi: {
      type: Boolean,
      default: false
    },
    onProgress: {
      type: Function,
      default: noop
    },
    onSuccess: {
      type: Function,
      default: noop
    },
    onError: {
      type: Function,
      default: noop
    }
  },
  data () {
    return {
      fileList: [], // 选中的文件
      reqs: {} // 正在上传的文件
    }
  },
  methods: {
    handleClick () {
      this.$refs.fileInput.click()
    },
    handleFileChange (evt) {
      const files = evt.target.files // 选中的文件
      if (files && files.length > 0) { // 直接自动上传
        this.upload(normalizeFiles(files))
      }
    },
    upload (files) {
      this.$refs.fileInput.value = null
      Array.prototype.forEach.call(files, (file, idx) => {
        if (this.fileList.every(f => f.uid !== file.uid)) {
          this.fileList.push(file) // 万一是重新刷新上传的
        } else { // 刷新上传

        }
        const data = new FormData()
        data.append(this.name, file)
        file.p = httpReq({
          method: 'post',
          url: this.url,
          data,
          onUploadProgress: e => {
            file.progress = Math.round((e.loaded * 100) / e.total) || 0
            this.onProgress(e, file)
          }
        })
        file.p.then((data) => {
          file.status = 'success'
          this.onSuccess(data)
        }, (err) => {
          file.status = 'error'
          this.onError(err)
        })
        /* setTimeout(() => {
          console.log(file.p)
          file.p.cancel('abort upload')
          console.log(file.p)
        }, 3000) */
      })
    },
    abortUpload (file) {
      console.log('abort')
      const idx = this.fileList.indexOf(file)
      if (idx >= 0) { // 讲道理不会没有吧...
        this.fileList.splice(idx, 1)
        // 如果处于上传中, abort axios
        if (file.status === 'loading') {
          file.p.cancel('用户取消了上传')
        }
      }
    },
    reUpload (file) {
      file.status = 'loading'
      file.progress = 0
      this.upload([file])
    }
  }
}
</script>

