<template>
    <div>
        <div class="ccb-process">
            <ul class="ccb-process-ul">
                <li v-for="(val,index) in processData.length - 1"
                    :style="{'width':liWidth, 'background-color': setUlColor(index+1)}"></li>
            </ul>
            <div class="ccb-process-div" v-for="(val,index) in processData"
                 :style="{'left': divLeft[index]}">
                <div :style="{'background-color': setDivColor(index)}">
                    <template v-if="checkStep(index)">
                        {{index+1}}
                    </template>
                    <template v-else>
                        <ccb-icon v-if="allStatus[index] === 'SUCCESS'" name="check"></ccb-icon>
                        <ccb-icon v-else name="guanbi"></ccb-icon>
                    </template>
                </div>
                <span :style="{'color': setDivColor(index)}">
                    {{val}}
                </span>
            </div>
        </div>
    </div>
</template>

<script>
import CcbIcon from '@/ui/basic/icon/icon'

export default {
  name: 'CcbProcess',
  components: {
    CcbIcon
  },
  props: {
    // 当前进度
    step: {
      type: Number,
      default: 1
    },
    processData: {
      type: Array,
      default: function () {
        return []
      }
    },
    status: {
      type: String,
      default: 'UNDONE',
      validator: function (s) {
        return ['UNDONE', 'SUCCESS', 'FAILURE'].indexOf(s) > -1
      }
    }
  },
  data () {
    return {
      colorMap: {
        'UNDONE': '#E6E6E6',
        'SUCCESS': '#3F79E5',
        'FAILURE': '#D72929'
      }
    }
  },
  computed: {
    validStep: function () {
      let len = this.processData.length
      if (this.step >= len) { return len } else if (this.step <= 1) { return 1 } else { return this.step }
    },
    allStatus: function () {
      let temp = []
      this.processData.forEach(function (val, index) {
        temp.push('UNDONE')
      })
      temp[this.validStep - 1] = this.status// 应用设置的当前状态
      for (let i = 0; i < this.validStep - 1; i++) {
        temp[i] = 'SUCCESS'
      }
      return temp
    },
    liWidth: function () {
      return (100 / (this.processData.length - 1) - 0.5) + '%'
    },
    divLeft: function () {
      let len = (this.processData.length - 1)
      let average = (800 - 7 * len) / len
      let leftArray = []
      let left = 0
      this.processData.forEach(function (val, index) {
        leftArray.push(left - 40 + 'px')
        left += average
      })

      return leftArray
    }
  },
  methods: {
    statusCheck: function (index, status) {
      return this.allStatus[index] === status
    },
    setUlColor: function (index) {
      if (this.validStep <= index) {
        return this.colorMap[this.allStatus[index - 1]]
      } else {
        return this.colorMap['SUCCESS']
      }
    },
    setDivColor: function (index) {
      return this.colorMap[this.allStatus[index]]
    },
    checkStep: function (index) {
      if (index + 1 > this.validStep) { return true } else if (index + 1 === this.validStep) { return this.status === 'UNDONE' } else { return false }
    }
  }
}
</script>

<style scoped>
    .ccb-process {
        position: relative;
        margin: 4px auto 2px auto;
        width: 800px;
        height: 10px;
    }

    .ccb-process-ul {
        list-style-type: none;
    }

    .ccb-process-ul li {
        height: 3px;
        margin: 10px auto;
        background-color: #999;
        float: left;
    }

    .ccb-process-div {
        position: absolute;
        margin-top: -6px;
        top: 5px;
        text-align: center;
        width: 100px;
    }

    .ccb-process-div div {
        width: 30px;
        height: 30px;
        background-color: #E6E6E6;
        -webkit-border-radius: 15px;
        line-height: 30px;
        margin: 0 auto;
        font-family: MicrosoftYaHei-Bold;
        font-size: 16px;
        color: #FFFFFF;
    }

    .ccb-process-div span {
        font-weight: bolder;
        color: #999999;
    }
</style>