<template>
  <transition name="ccb-zoom-in-top">
    <div v-append-to-body class="ccb-option" v-show="visible">
      <ul v-for="(opts, idx1) of displayOpts" :class="['ccb-option-ul', {
        'is-cascade': cascade
      }]">
        <li v-for="(opt, idx2) of opts" :key="opt.value" @click="chooseOpt(opt, idx1, idx2)" class="ccb-option-li"
          :class="[{
            'is-selected': opt.selected,
            'is-disabled': opt.disabled
          }]"
        >
          {{opt.label}}
        </li>
      </ul>
    </div>
  </transition>
</template>

<script>
/**
 * 挂载在body(absolute)下面的选项框组件, 用于menu、select等
 * 1. 允许层级关系(多级展开, options包含children)
 * 2. 允许异步加载(发请求啥的之类的)
 * 3. 允许过滤(允许自定义添加过滤方法)
 */

import appendToBody from '@/directive/append-to-body'
import { deepClone } from '@/tools/util'
import popupMixin from '@/mixin/popup-mixin'

const initializeOpts = function (opts) {
  recursionOpts(opts, (opt, parent) => {
    opt.parent = parent // cascade的场景下方便追溯父值
    opt.selected = opt.selected || false
    opt.disabled = opt.disabled || false
  })
  return opts
}

/* const addtionalAttr = ['selected', 'disabled', 'parent'] */

const recursionOpts = function (opts = [], cb, parent) {
  opts.forEach(opt => {
    cb(opt, parent)
    recursionOpts(opt.children, cb, opt)
  })
}

// 只在同层级里找
const mergeOpts = function (targetOpts = [], sourceOpts = []) {
  targetOpts.forEach(opt1 => {
    const sourceOpt = sourceOpts.find(opt2 => opt2.value === opt1.value)
    if (sourceOpt) {
      opt1.selected = sourceOpt.selected
      opt1.disabled = sourceOpt.disabled
      mergeOpts(opt1.children, sourceOpt.children) // 再找下一层
    }
  })
}

export default {
  name: 'ccb-option',
  directives: {
    appendToBody
  },
  mixins: [popupMixin],
  props: {
    value: {

    },
    options: {
      type: Array,
      required: true
    },
    filter: {
      type: Boolean,
      default: false
    },
    searchFn: {
      type: Function
    },
    multi: {
      type: Boolean,
      default: false
    },
    visible: {
      type: Boolean,
      default: false
    },
    selectEveryLevel: { // 多层级选项时，是否选中父选项后直接展示
      type: Boolean,
      default: false
    },
    // 需要显示声明是考虑到异步加载, 并不能通过children判断是不是级联
    cascade: {
      type: Boolean,
      default: false
      /* validator (v) {
        if (v && this.multi) {
          console.error('ccb-option: 不允许同时设置cascade和multi为true') // 感觉太麻烦了
          return false
        }
        return true
      } */
    }
  },
  data () {
    return {
      displayOpts: '',
      /**
       * 选项最终值，可能有多重格式
       * 1. 多选时, 格式是: Array<Object> 如下:
       * [{value:'value1',label:'label1'}, {value:'value2', label:'label2'} ...]
       * 2. 单选+级联时, 格式是: Array<Object> 如下:
       * [{value:'value1',label:'label1'}, {value:'value1-1', label:'label1-1'} ...]
       * 3. 单选+非级联时, 格式是 Object 如下:
       * {value:'value1',label:'label1'}
       */
      selectValue: (this.multi || this.cascade) ? [] : {}
      // cascade: false
    }
  },
  computed: {
    /* displayOpts () {
      return this.opts.slice(0, 1)
    } */
  },
  watch: {
    options: {
      immediate: true,
      handler (options) { // 如果options被应用改动了
        const newOpts = initializeOpts(deepClone(this.options))
        mergeOpts(newOpts, this.displayOpts[0]) // 将之前的状态merge进去
        this.displayOpts = [newOpts] // 重新从0层级开始展开?
      }
    },
    visible (visible) {
      if (visible) {
        this.$ccb_updatePopup()
      }
      // 关闭并且是级联的情况下, 还原展示层级到当前值
      if (!visible && this.cascade) {
        this.displayOpts.splice(1)
        // 选中全部还原
        let currentLevel = this.displayOpts[0]
        recursionOpts(currentLevel, child => { child.selected = false })
        // 根据当前值重新设置selected
        this.value && this.value.forEach(val => {
          let currentOpt = currentLevel.find(currentOpt => currentOpt.value === val.value)
          currentOpt.selected = true
          currentLevel = currentOpt.children
          if (currentLevel) {
            this.displayOpts.push(currentLevel)
          }
        })
      }
    }
  },
  methods: {
    chooseOpt (opt) {
      this.alterSelectedState.apply(this, arguments)
      // 单选且没有下一个层级, 那么就当做选中了然后隐藏
      if (!this.multi && !opt.children) {
        this.$emit('update:visible', false)
      }
      // 点的不是最后一个级联框
      while (this.displayOpts[this.displayOpts.length - 1].indexOf(opt) === -1) {
        this.displayOpts.pop()
      }
      // 层级选项, 展示后边的层级
      if (opt.children) {
        this.displayOpts.push(opt.children)
      } else if (this.multi) { // 多选
        this.selectValue = this.selectValue || [] // 万一应用动态改模式，比如单选改多选了
        const idx = this.selectValue.indexOf(opt)
        if (idx > -1) {
          this.selectValue.splice(idx, 1) // 移除
        } else {
          this.selectValue.push(opt)
        }
        this.normalizValue()
      } else { // 单选
        this.selectValue = opt
        this.normalizValue()
      }
    },
    // 由于改变了原来的数据, 所以不直接返回selectValue, 加工下返回给v-model
    normalizValue () {
      let emitValue
      if (this.multi) {
        emitValue = this.options.filter(opt => this.selectValue.some(sv => sv.value === opt.value))
      } else {
        if (this.cascade) { // 级联的情况
          emitValue = []
          let cascadeValue = [this.selectValue]
          let parent = this.selectValue.parent
          // 按照子节点到父节点的顺序装入数组
          while (parent) {
            cascadeValue.push(parent)
            parent = parent.parent
          }
          let sameLevelOpts = this.options
          // 按照父节点到子节点的顺序把选中值排列，并且是从this.options里面去选择(应用的原始数据)
          while (cascadeValue.length > 0) {
            let item = cascadeValue.pop()
            sameLevelOpts = sameLevelOpts.find(sameLevelOpt => item.value === sameLevelOpt.value)
            emitValue.push(sameLevelOpts)
            sameLevelOpts = sameLevelOpts.children
          }
        } else {
          emitValue = this.options.find(opt => opt.value === this.selectValue.value) // 理应只有一个
        }
      }
      this.$emit('input', emitValue)
    },
    /*
      当点击某一个选项时，动态的去修改selected状态
      1. 多选情况
      2. 级联情况
    */
    alterSelectedState (opt, idx1, idx2) {
      if (this.multi) {
        opt.selected = !opt.selected // 多选就反选下咯
      } else { // 单选, 清除自己所在层级
        this.displayOpts[idx1].forEach(sameLevelOpt => {
          if (sameLevelOpt !== opt) {
            sameLevelOpt.selected = false
          } else {
            sameLevelOpt.selected = true
          }
        })
      }
      // 子层级的取消选择, 得递归了
      recursionOpts(opt.children, child => { child.selected = false })
    }
  },
  mounted () {
    // 托管选择框(动态修改z-index, 监听页面滚动等事件动态改变位置)
    this.$ccb_initPopup(this.$parent.$el, this.$el, { fitWidth: !this.cascade })
  },
  beforeDestroy () {
    this.$ccb_destroyPopup()
  }
}
</script>

