export function parseUnit (str, suffix = 'px') {
  if (!isNaN(str)) { // '100', 100...
    return str + suffix
  }
  if (!isNaN(parseInt(str, 10))) { // '100%', '100px', '100rem'...
    return str
  }
  return str // 'auto', 'inherit'...
}

export function createStyleNode (cssStr) {
  const style = document.createElement('style')
  style.type = 'text/css'
  if (style.styleSheet) {
    style.styleSheet.cssText = cssStr
  } else {
    style.appendChild(document.createTextNode(cssStr))
  }
  return style
}
