
<script>
import prev from './prev'
import size from './size'
import jump from './jump'
import next from './next'
import page from './page'

export default {
  name: 'ccb-pagination',
  components: {
    prev,
    size,
    jump,
    next,
    page
  },
  props: {
    current: {
      type: Number,
      default: 1,
      validator: v => v >= 1
    },
    pageSizeList: {
      type: Array,
      default: () => [5, 10, 20, 50],
      validator: v => v.every(n => typeof (n) === 'number')
    },
    pageSize: {
      type: Number,
      default: 5
    },
    total: {
      type: Number,
      default: 0
    },
    layout: {
      type: Array,
      default: () => ['total', 'size', 'prev', 'page', 'next', 'jump']
    }
  },
  data () {
    return {
      /**
       * current和pagesize两个属性既要通过prop传，又要在本地修改使用
       * 如果用户没有.sync, 那依照规范又不能自己修改prop -_-||
       * 所以本地做一个内部用的属性,没找到更好的姿势
       */
      internalCurrent: this.current,
      internalPageSize: this.pageSize,
      prevDisabled: false,
      nextDisabled: false
    }
  },
  watch: {
    // 要是用户修改了, 或者配置了.sync
    current: {
      immediate: true,
      handler (val) {
        this.internalCurrent = val
      }
    },
    // 要是用户修改了, 或者配置了.sync
    pageSize: {
      immediate: true,
      handler (val) {
        this.internalPageSize = val
      }
    },
    internalCurrent: {
      handler (current) {
        if (current > 1 && current < this.total) {
          this.prevDisabled = this.nextDisabled = false
        } else if (current === 1) {
          this.prevDisabled = true
          this.nextDisabled = false
        } else if (current === this.total) {
          this.prevDisabled = false
          this.nextDisabled = true
        }
      }
    }
  },
  render () {
    const container = <div class="ccb-pagination"></div>
    const compList = {
      'total': <span class="el-pagination-total">共 {this.total} 条</span>,
      'size': <size value={this.internalPageSize} on-input={this.handleSizeChange} options={this.pageSizeList}></size>,
      'prev': <prev on-click={this.handleClickPrev} disabled={this.prevDisabled}></prev>,
      'page': <page on-page-change={this.handlePageChange} currentPage={this.internalCurrent} totalPage={this.total}></page>,
      'next': <next on-click={this.handleClickNext} disabled={this.nextDisabled}></next>,
      'jump': <jump on-jump-change={this.handlePageChange} totalPage={this.total} currentPage={this.internalCurrent}></jump>
    }
    container.children = []
    this.layout.forEach(compNm => {
      if (compList[compNm]) {
        container.children.push(compList[compNm])
      }
    })
    return container
  },
  methods: {
    handleSizeChange (v) {
      this.$emit('update:pageSize', v)
      this.$emit('page-size-change', v)
    },
    handleClickPrev () {
      this.internalCurrent = this.internalCurrent - 1
      this.$emit('update:current', this.internalCurrent)
      this.$emit('click-prev', this.internalCurrent)
    },
    handleClickNext () {
      this.internalCurrent = this.internalCurrent + 1
      this.$emit('update:current', this.internalCurrent)
      this.$emit('click-next', this.internalCurrent)
    },
    handlePageChange (newPageNumber) {
      this.internalCurrent = newPageNumber
      this.$emit('update:current', this.internalCurrent)
    }
  }
}
</script>

