</template>

<script>
import CcbButton from '@/ui/basic/button/button'
import CcbIcon from '@/ui/basic/icon/icon'

export default {
  name: 'next',
  components: {
    CcbButton,
    CcbIcon
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    handleClick (e) {
      this.$emit('click', e)
    }
  }
}
</script>

