<template>
  <transition name="ccb-zoom-in-top">
    <div v-append-to-body class="ccb-date-option" v-show="visible">
      <div class="ccb-date-option-header">
        <div class="ccb-date-option-op" @click="jump">
          <ccb-icon dPrev name="double-arrow-left"></ccb-icon>
          <ccb-icon prev name="arrow-left"></ccb-icon>
          <span class="ccb-date-option-middle">{{middleTitle}}</span>
          <ccb-icon next name="arrow-right"></ccb-icon>
          <ccb-icon dNext name="double-arrow-right"></ccb-icon>
        </div>
        <div class="ccb-date-option-day">
          <span>日</span>
          <span>一</span>
          <span>二</span>
          <span>三</span>
          <span>四</span>
          <span>五</span>
          <span>六</span>
        </div>
      </div>
      <div class="ccb-date-option-content">
        <span
          @click="chooseDate(dateInfo)"
          v-for="dateInfo of this.contentInfo.totalDays"
          :key="dateInfo.date.getTime()"
          :class="[{
            'is-current': dateInfo.type === 'current'
          }]"
        >
          {{dateInfo.dateNum}}
        </span>
      </div>
    </div>
  </transition>
</template>

<script>
import CcbIcon from '@/ui/basic/icon/icon'
import appendToBody from '@/directive/append-to-body'
import popupMixin from '@/mixin/popup-mixin'
import { isDate } from '@/tools/date'
/**
 * 得到当前日期所在月份如下相关信息(渲染当前月份日历需要)
 * 1. 当前月份总天数
 * 2. 当前月份第一天是周几
 * 3. 当前月份最后一天是周几
 * 4. 若第一天不是周日, 那么本月第一天到上个月最后一个周日之间的日期
 * 5. 若最后一天不是周六, 那么这个月最后一天到下个月第一个周六之间的日期
 * 6. 本月的日期
 * @param {*} date
 * @returns info
 *          ..dayCounts 本月总天数
 *          ..lastDay 本月最后一天周几
 *          ..firstDay 本月第一天周几
 *          ..preMonthDays Array [{dateNum: 3, date: Date}...]
 *            date是Date, dateNum是几号
 *          ..currentMonthDays Array 本月的
 *          ..nextMonthDays Array
 */
const getMonthInfo = function (date) {
  // 判断一哈
  if (!isDate(date)) {
    throw new Error('ccb-date-option,getMonthInfo: ' + date + ' is not a correct date object')
  }
  var info = {}
  var date1 = new Date(date) // clone一份

  /** 开始获取 1.:当前月份总天数 */
  var m = date1.getMonth()
  date1.setMonth(m + 1)
  date1.setDate(0) // 这样把日期转化为当月最后一天
  info.dayCounts = date1.getDate() // 获取总天数

  /** 开始获取 3.:当前月份最后一天是周几 */
  info.lastDay = date1.getDay()

  /** 开始获取 5.:这个月最后一天到下个月第一个周六之间的日期 */
  info.nextMonthDays = []
  if (info.lastDay !== 6) { // 本月最后一天不是周6
    date1.setDate(date1.getDate() + 1) // 下个月第一天
    while (date1.getDay() !== 0) { // 取出下个月第一天开始的日期直到第一个周六
      info.nextMonthDays.push({
        dateNum: date1.getDate(),
        date: date1,
        // date: date1,
        // day: date1.getDay(),
        type: 'next'
      })
      date1 = new Date(date1)
      date1.setDate(date1.getDate() + 1) // 往后一天
    }
  }

  var date2 = new Date(date) // clone一份
  /** 开始获取 2.: 当前月份第一天是周几 */
  date2.setDate(0)
  date2.setDate(date2.getDate() + 1)
  info.firstDay = date2.getDay()

  var date3 = new Date(date2)

  /** 开始获取 6.: 本月的日期 */
  info.currentMonthDays = []
  for (var i = 0; i < info.dayCounts; i++) {
    info.currentMonthDays.push({
      dateNum: date2.getDate(),
      date: date2,
      // d: formatDate(date2),
      // date: date2.getDate(),
      // day: date2.getDay(),
      type: 'current'
    })
    date2 = new Date(date2)
    date2.setDate(date2.getDate() + 1)
  }

  /** 开始获取 4.: 本月第一天到上个月最后一个周日之间的日期 */
  info.preMonthDays = []
  if (info.firstDay !== 0) { // 本月第一天不是周日
    date3.setDate(date3.getDate() - 1) // 上个月最后一天
    while (date3.getDay() !== 6) { // 取出上个月的日期直到周日
      info.preMonthDays.unshift({
        dateNum: date3.getDate(),
        date: date3,
        // d: formatDate(date3),
        // date: date3.getDate(),
        // day: date3.getDay(),
        type: 'prev'
      })
      date3 = new Date(date3)
      date3.setDate(date3.getDate() - 1)
    }
  }
  info.totalDays = info.preMonthDays.concat(info.currentMonthDays).concat(info.nextMonthDays)
  return info
}

export default {
  name: 'ccb-date-option',
  components: {
    CcbIcon
  },
  directives: {
    appendToBody
  },
  mixins: [popupMixin],
  props: {
    value: {
      type: Date,
      default: new Date(),
      validator: v => isDate(v)
    },
    type: { // 先只写日期吧，以后估计还有周、月各种鬼。。
      type: String,
      default: 'date'
    },
    multi: {
      type: Boolean,
      default: false
    },
    visible: {
      type: Boolean,
      default: false
    }
    /* displayFormat: {
      type: String,
      default: 'yyyyMMdd'
    },
    valueFormat: {
      type: String,
      default: 'yyyyMMdd'
    } */
  },
  data () {
    return {
      panelDate: '' // 用timestamp, 为了触发修改
    }
  },
  watch: {
    value: {
      immediate: true,
      handler (value) {
        this.panelDate = value.getTime()
      }
    },
    visible (visible) {
      this.panelDate = this.value.getTime()
    }
  },
  computed: {
    contentInfo () {
      return getMonthInfo(new Date(this.panelDate))
    },
    middleTitle () {
      const date = new Date(this.panelDate)
      return date.getFullYear() + '年' + (date.getMonth() + 1) + '月'
    }
  },
  mounted () {
    // 托管选择框(动态修改z-index, 监听页面滚动等事件动态改变位置)
    this.$ccb_initPopup(this.$parent.$refs['inputRef'].$el, this.$el)
  },
  beforeDestroy () {
    this.$ccb_destroyPopup()
  },
  methods: {
    chooseDate (dateInfo) {
      if (!this.multi) {
        this.$emit('update:visible', false)
      }
      this.$emit('input', dateInfo.date)
    },
    jump ({ target }) {
      const date = new Date(this.panelDate)
      if (target.hasAttribute('prev')) {
        date.setMonth(date.getMonth() - 1)
      } else if (target.hasAttribute('dPrev')) {
        date.setFullYear(date.getFullYear() - 1)
      } else if (target.hasAttribute('next')) {
        date.setMonth(date.getMonth() + 1)
      } else if (target.hasAttribute('dNext')) {
        date.setFullYear(date.getFullYear() + 1)
      }
      this.panelDate = date.getTime()
    }
  }
}
</script>

