<template>
  <button :disabled="disabled" :type="htmlType" :class="['ccb-button',
    'ccb-button-' + type ,
    'ccb-button-' + size,
    {
      'is-disabled': disabled
    }
    ]"
    @click="click"
  >
    <ccb-icon :name="icon" v-if="icon"></ccb-icon>
    <span v-if="$slots.default"><slot/></span>
  </button>
</template>

<script>
import CcbIcon from '@/ui/basic/icon/icon'

export default {
  components: {
    CcbIcon
  },
  name: 'ccb-button',
  props: {
    type: {
      type: String,
      default: 'default'
    },
    size: {
      type: String,
      default: 'default',
      validator: v => ['default', 'medium', 'small', 'mini'].indexOf(v) > -1
    },
    htmlType: {
      type: String,
      default: 'button'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    icon: {
      type: String
    }
  },
  methods: {
    click (e) {
      this.$emit('click', e)
    }
  }
}
</script>
