<template>
  <div v-append-to-body v-show="show" class="ccb-window">
    <div class="ccb-window-header">
      <span>{{title}}</span>
      <ccb-icon name="close" @click="closeWindow"></ccb-icon>
    </div>
    <div class="ccb-window-content">
      <slot></slot>
    </div>
    <div class="ccb-window-footer">
      <slot name="footer">
      </slot>
    </div>
  </div>
</template>

<script>
import modalMixin from '@/mixin/modal-mixin'
import appendToBody from '@/directive/append-to-body'
import CcbIcon from '@/ui/basic/icon/icon'

export default {
  name: 'ccb-window',
  components: {
    CcbIcon
  },
  directives: {
    appendToBody
  },
  mixins: [modalMixin],
  props: {
    show: {
      type: Boolean,
      default: true
    },
    title: {
      type: String
    }
  },
  watch: {
    show: {
      immediate: true,
      handler (val) {
        if (val) {
          this.openModal(this)
        } else {
          this.closeModal(this)
        }
      }
    }
  },
  methods: {
    closeWindow () {
      this.$emit('update:show', false)
    }
  }
}
</script>

